@charset "UTF-8";
@white: #FFFFFF;
@primary-color: #61AEE4;
@blue: #93D2FA;
@secondary-color: #BBBBBB;
@grey: #999999;

.Button {
    border: 1px solid @primary-color; 
    color: @white; 
    font-size: 1rem;
    letter-spacing: .5px;
    text-align: center;
    text-transform: uppercase;
    background: transparent;
    .Transition-3s;
}
.ButtonShape {
    border-radius: 10px;
    height: 55px;
    line-height: 53px;
}
.ButtonShapeMobile {
    border-radius: 7px;
    height: 42px;
    padding: 12px 8px 8px;
    width: 100%;
}
.ButtonHover {
	cursor: pointer;
    background-color: @primary-color;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-box-shadow: 0 0 10px black inset;    
    -moz-box-shadow: 0 0 10px black inset;    
    -ms-box-shadow: 0 0 10px black inset;    
    -o-box-shadow: 0 0 10px black inset;
    box-shadow: 0 0 10px black inset;
}
.Transition-3s {
    -webkit-transition: all 0.3s linear;
    -moz-transition: all 0.3s linear;
    -ms-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;   
}
* { margin: 0}
html, body {
    font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
    /* font-family: "Orbitron", "Aldrich"; */
	font-size: 16px;
	background: -webkit-linear-gradient(#222222 30%, #444444 60%, #666666 90%) no-repeat;
	background: -moz-linear-gradient(#222222 30%, #444444 60%, #666666 90%) no-repeat;
	background: -ms-linear-gradient(#222222 30%, #444444 60%, #666666 90%) no-repeat;
	background: -o-linear-gradient(#222222 30%, #444444 60%, #666666 90%) no-repeat;
	background: linear-gradient(#222222 30%, #444444 60%, #666666 90%) no-repeat;
	background-color: #666666; 
    height: 100%;
	-webkit-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	-o-text-size-adjust: 100%;
	text-size-adjust: 100%;
}
article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary { display: block
}
audio, canvas, progress, video { display: inline-block; vertical-align: baseline
}
audio:not([controls]) { display: none; height: 0
}
#main { width: 100%; height: 100% !important; margin-bottom: -50px;} 

/* TEXT */

h1.title, h2.title { font-family: "Orbitron" !important; font-size: 1.7rem; font-weight: normal; text-transform: lowercase; text-shadow: 2px 2px 4px #000; padding: 0; margin: 20px 0; clear: both;
    span { display: block; float: left;}
}
