h3 { color: @grey; font-size: 1.45rem; margin: 0 0 5px 0; line-height: 1.8rem; text-transform: uppercase; font-style: italic; text-shadow: 1px 2px 1px #111111;}
p, #content ul { font-size: 1.05rem; color: @white; text-shadow: 1px 1px 1px #111111;}
p {	line-height: 1.5rem; margin: 0 0 17px 0;}
.skills p { margin: 0 0 10px 0;
    &:last-child { margin-bottom: 17px}
}
.skills .type p { font-weight: bold; text-align: right; margin-right: 5px; color: @secondary-color;}
.job h3 { color: @white;}
#folio h3, #form h3 { margin: 20px 0 5px;
    &:first-child { margin-top: 0 !important}
}
footer p { margin: 0; font-size: .77rem; line-height: 1rem; letter-spacing: .5px;}
ul.job-des { float: left; clear: left; margin: 0; padding: 0;
    li { margin: 0 0 0 18px; list-style: square;}
}
.quote { font-size: 1.45rem; line-height: 1.8rem; margin-bottom: 20px; text-align: center;
}
.white { color: @white}
.blue { color: @primary-color}
.grey { color: @secondary-color}

/* Links */

#content a:link, #content a:visited, footer a:link, footer a:visited { color: @white; text-decoration: none; border-bottom: 1px dotted @primary-color}
#content a:hover, #nav-main a:hover, footer a:hover, .intro a:hover, #proj-launch a:hover, a#all:hover { -webkit-transition: .7s; -moz-transition-duration: .7s; -ms-transition-duration: .7s; -o-transition-duration: .7s; transition-duration: .7s; color: @primary-color !important; cursor: pointer; text-decoration: none}
#content a:hover, footer a:hover { border-bottom: none}
li.st_sharethis_large span.stButton { display: none !important;}

/* GENERAL */

.no-padding {padding: 0 !important;}
img {margin: 0;	padding: 0;	border: 0;}
table.skills td { vertical-align: top}

/* SVG */

.cls-1 { fill: @secondary-color;}
.cls-2 { fill: @primary-color;}
.cls-3 { fill: @white;}

/* Clearfix */

.clearfix:after {
content: ".";
display: block;
height: 0;
clear: both;
visibility: hidden;}

.clearfix { display: inline-block;}
* html .clearfix {height: 0;}
.clearfix {display: block;}

/* HEADER & NAVIGATION */

header, .header-wrapper { webkit-backface-visibility: hidden; backface-visibility: hidden;}
header { width: 100%; box-shadow: 0px 7px 15px #171717; background-color: #0C0E10; border-bottom: 1px solid #333333; .Transition-3s;
    &.sticky { position: fixed; left: auto; top: 0; z-index: 200;
        hgroup { padding-top: 9px;
            .title { margin-left: 11px}
            h1 { font-size: 1.3rem; margin: 13px 0 -3px;}
            h2 { display: none}
        }
        #nav-main { background-position-y: -10px;
            ul.nav li a { padding: 23px 15px 19px;}
        }
    }
}
hgroup { padding-left: 15px;
    img { .Transition-3s;}
    img, .title { display: block; float: left;}
    .title { margin-left: 13px}
    h1, h2 { .Transition-3s;}
    h1 { font-size: 1.7rem; text-transform: capitalize; letter-spacing: 2px; margin: 15px 0 -5px; color: @white; font-weight: normal; font-family: "Orbitron";
        /* transform: scale(1.0, 1.3);
        -webkit-transform: scale(1.0, 1.3);
        -moz-transform: scale(1.0, 1.3);
        -ms-transform: scale(1.0, 1.3);
        -o-transform: scale(1.0, 1.3); */}
    h2 { font-size: .7rem; text-transform: uppercase; letter-spacing: .5px; margin: 0 0 0 1px; color: @white; font-weight: normal;}
}
#nav-main { width: auto; background: url(../images/bck-nav-main.png) repeat-x;
    ul.nav { display: block; margin: 0; padding: 0;
        li a { font-family: "Orbitron"; color: @white; text-transform: capitalize;}
        li.active a { color: @grey;}
    }
	hgroup { float: left}
}
#nav-top { width: 100%; height: 32px; border-bottom: 1px solid #030303;
    background: linear-gradient(#434951, #5b5f63 42%, #747474 84%) no-repeat;
	background: -webkit-linear-gradient(#434951, #5b5f63 42%, #747474 84%) no-repeat;
	background: -moz-linear-gradient(#434951, #5b5f63 42%, #747474 84%) no-repeat;
	background: -ms-linear-gradient(#434951, #5b5f63 42%, #747474 84%) no-repeat;
	background: -o-linear-gradient(#434951, #5b5f63 42%, #747474 84%) no-repeat;
	background-color: #5b5f63;
}
#navSocial { padding-top: 1px;
    ul { display: block; padding: 0; margin: 0;
        li { float: left; width: 30px; line-height: 30px; margin-right: 1px; padding: 0; display: block; list-style: none; background-color: #747474; text-align: center;}
        > li a { float: left; background: none repeat scroll 0 0 #747474; width: 100%; height: 100%; line-height: 30px;
            -webkit-transition: all 0.3s linear;
            -moz-transition: all 0.3s linear;
            -ms-transition: all 0.3s linear;
            -o-transition: all 0.3s linear;
			transition: all 0.3s linear;}
        li a:hover { background-color: @primary-color}
        > li a i { margin: 0; color: #ffffff; font-size: .8rem;
            -webkit-transition: all 0.3s linear;
            -moz-transition: all 0.3s linear;
            -ms-transition: all 0.3s linear;
            -o-transition: all 0.3s linear;
			 transition: all 0.3s linear;}
    }
}
#navContact {
    ul { display: block; padding: 0; margin: 0;}
}

/* CONTENT */

.intro { width: auto; text-align: center; max-width: 700px; margin: 20px auto; padding: 0 10px;
    p.detail { margin-top: 25px}
    .media { position: relative;
        .image { width: 100%; height: auto}
        p { position: absolute}
    }
}
#content { padding-top: 17px;
    &.scroll { max-height: 560px; overflow: scroll; overflow-x: hidden;}
    #projects-field { margin-top: 9px;}
    #thank-you { text-align: center; padding-top: 25px;
        .quote { text-transform: uppercase;}
    }
    .job { width: 100%; margin: 10px 0 20px; float: left; clear: both;}
    .job-title { width: 70%; float: left; clear: both;}
}

/* PORTFOLIO */

#folio {
	#fb-viewer { cursor: -webkit-zoom-in; cursor: -moz-zoom-in; cursor: zoom-in;
        img { border: @white 1px solid}
    }
	#thumb-nav { clear: right; height: 75px; margin: 0 auto; border: none;
        img { border: none; float:left; height:75px; width: 75px; -webkit-box-shadow: 0 0 10px #000000; -moz-box-shadow: 0 0 10px #000000; -ms-box-shadow: 0 0 10px #000000; -o-box-shadow: 0 0 10px #000000; box-shadow: 0 0 10px #000000;}
        a img { -webkit-filter: grayscale(100%); -moz-filter: grayscale(100%); -ms-filter: grayscale(100%); -o-filter: grayscale(100%); filter: grayscale(100%);
            &:hover { -webkit-outline: 3px solid #ffa010; -moz-outline: 3px solid #ffa010; -ms-outline: 3px solid #ffa010; -o-outline: 3px solid #ffa010; outline: 3px solid #ffa010; -webkit-filter: grayscale(0%); -moz-filter: grayscale(0%); -ms-filter: grayscale(0%); -o-filter: grayscale(0%); filter: grayscale(0%)}         
        }
    }
    #proj-imgs { padding-left: 15px; 
        img.mnimg { -webkit-box-shadow: 0 0 10px #000000; box-shadow: 0 0 10px #000000;}
    }
    .disclaimer { font-size: .5rem; font-style: italic}
    #proj-viewer, #proj-launch { 
        float: left;
        border-radius: 10px;
        border: 1px solid #303030;
        letter-spacing: -0.5px;
        height: 55px;
        margin: 7px 0;
        padding: 0;
        background: transparent;
        display: block;
        &:hover { .ButtonHover;}
    }
    #proj-viewer { width: 180px; padding: 0 5px;}
    #proj-launch { width: 138px; margin-right: 10px;
        a {	padding: 0 15px}
    }
    #proj-share { clear: left; margin-top: 160px}
    a.control { margin-top: 12px; width: 30px; height: 31px;  background-position: 0 0 no-repeat; overflow: hidden; text-indent: -999999px; display: block;
        &#next { background-image: url(../images/folio-view-next.png); float: right;}        
        &#back { background-image: url(../images/folio-view-back.png); float: left;}
        &:hover {background-position: 0 -31px}
    }
	a#all {float: left;}
	a#all.projects {width: 120px;}
    a#all, #proj-launch a {
	text-decoration: none;
	font: 16px/55px "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
	text-transform: uppercase;
	letter-spacing: -0.6px;
	margin: 0 auto;
	text-align: center;
	display: block;
    }
}
.th2 { width: 170px}
.th3 { width: 255px}

/* FORM */

#form {
    fieldset { display: block; border: none;}
    .chat { display: inherit;
        ul { margin: 10px 0 0 0; padding: 0;
            li { font-size: 18px; letter-spacing: 0.5px; margin: 0 0 7px 0;
                a:before { content: ""; float: left; display: block; margin-right: 7px; height: 21px; width: 21px;}
                a.phone:before { background: url(../images/icon-phone.png) no-repeat;}
                a.email:before { background: url(../images/icon-email.png) no-repeat; margin-top: -1px;}
            }
        }
        a img {
    	    border: 1px solid transparent;
	        border-radius: 10px;
	        -webkit-transition-property: border;
	        -moz-transition-property: border;
	        -o-transition-property: border;
	        transition-property: border;
	        -webkit-transition-duration: .7s;
	        -moz-transition-duration: .7s;
	        -o-transition-duration: .7s;
	        transition-duration: .7s;
	        -webkit-box-sizing: border-box;
	        -moz-box-sizing: border-box;
	        -ms-box-sizing: border-box;
	        -o-box-sizing: border-box;
	        box-sizing: border-box;
	        &:hover { border-color: @primary-color;
	            color: @white;
	            outline: 0;
	            background-color: @secondary-color
            }
        }
    }
    .notes {
        p {	margin: 0 0 10px 0}
    }
    input::-webkit-input-placeholder, input:-moz-placeholder, input:-ms-placeholder, input:-o-placeholder, textarea::-webkit-input-placeholder, textarea:-moz-placeholder, textarea:-ms-placeholder, textarea:-o-placeholder { color: #555555}
}
.submit { .Button;
    &:hover { .ButtonHover;}
}
input, textarea {
    border-radius: 10px;
    -webkit-box-shadow: 0 0 10px black inset;
    -moz-box-shadow: 0 0 10px black inset;
    -ms-box-shadow: 0 0 10px black inset;
    -o-box-shadow: 0 0 10px black inset;
    box-shadow: 0 0 10px black inset;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition-property: border;
    -moz-transition-property: border;
    -ms-transition-property: border;
    -o-transition-property: border;
    transition-property: border;
    -webkit-transition-duration: .7s;
    -moz-transition-duration: .7s;
    -ms-transition-duration: .7s;
    -o-transition-duration: .7s;
    transition-duration: .7s;
    background: #222222;
    border: 1px solid #303030;
    color:#555555;
    display:block;
    font: normal 20px;
    letter-spacing:-0.5px;
    padding:16px;
        &:focus { border-color: @secondary-color;
            color: @white;
            outline: 0;
            background-color: #2C2C2C
            }
    }
.form-field label, .form-verify label { display:none;}

